import { Table } from "react-bootstrap";
import { useSelector, useDispatch } from "react-redux";
import { changeName, plusAge } from "./../store/userSlice";
import { addCount } from "./../store.js";

function Cart() {
  let state = useSelector((state) => {
    console.log(1111, state);
    return state; // state.cart 도 가능
  }); // redux store 가져옴
  let dispatch = useDispatch();

  return (
    <div>
      <div>
        {state.user.name} {state.user.age}의 장바구니
      </div>
      <button
        onClick={() => {
          dispatch(plusAge());
        }}
      >
        나이증가
      </button>
      <Table>
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경하기</th>
          </tr>
        </thead>
        <tbody>
          {state.cart.map((prod, index) => {
            return (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{prod.name}</td>
                <td>{prod.count}</td>
                <td>
                  <button
                    onClick={() => {
                      dispatch(addCount(prod.id));
                    }}
                  >
                    +
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
}

export default Cart;
